.headerdiv {
    text-align: center;
    margin: 0 auto;
    min-width: 700px;
}

.title {
    color: #91A788;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    font-family: "Partial Sans KR";
    font-size: clamp(32px, 8vw, 80px);
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 1.6px;
    margin-bottom: 8px;
    margin-top: 40px;
}

.subtitle {

    color: #595959;
    font-family: "NexonLv1Gothic";
    font-size: clamp(10px, 2.5vw, 18px);
    font-style: normal;
    font-weight: 700;
    line-height: normal;
}
.topbar{
    height: 28px;
    background: #91A788;
}