:root {
  --list-w: 900px;
}

.mainWrapper {
  display: flex;
  flex-direction: column;
  margin: 0;
  margin-top: 0;
  /* padding-top: 15px; */
  min-width: 700px;
  width: 100%;
  height: 100vh;
  overflow-x: auto;
  /* ← 가로 스크롤 허용 */
  overflow-y: hidden;
}

.main {
  position: relative;
  align-items: center;
  display: flex;
  flex-direction: column;
  min-height: 0;
  margin: 0 auto;
  height: 100%;
  min-width: 880px;
}

/* ===== 검색바 ===== */
.searchInner {
  margin: 0 auto;
  width: 900px;
  height: 60px;
  background: rgba(255, 255, 255, 0.70);
  border-radius: 999px;
  border: 0.5px solid rgba(151, 151, 151, 0.4);
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 0 16px;
  margin-top: 10px;
}

.searchInner {
  display: flex;
  gap: 10px;
}

.searchIcon {
  width: 70px;
  height: 46px;
  border: none;
  border-radius: 20px;
  background: rgba(211, 211, 211, 0.482);
  border: 0.5px solid rgba(151, 151, 151, 0.4);
}

.searchimg {
  height: 30px;
}

.categoryBtn {
  white-space: nowrap;
}

/* .categorySelect{ border:none; font-size: large; background: rgba(255,255,255,.20); } */
.categorySelect {
  font-size: 15px;
  font-weight: 600;
  color: #555;
  background-color: rgba(255, 255, 255, 0.7);
  padding: 10px 30px 10px 15px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 25px;
  cursor: pointer;
  outline: none;

  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;

  background-image: url('data:image/svg+xml;charset=UTF-8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="none" stroke="%23888888" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"><path d="m6 8 4 4 4-4"/></svg>');
  background-repeat: no-repeat;
  background-position: right 12px center;
  background-size: 14px;
}

.categorySelect:focus {
  border-color: rgba(173, 211, 173, 0.89);
  box-shadow: 0 0 0 2px rgba(127, 185, 127, 0.1);
}

.searchInput {
  flex: 1 1 auto;
  min-width: 0;
  height: 44px;
  padding: 0 14px;
  box-sizing: border-box;
  font-size: large;
  background: rgba(255, 255, 255, .20);
  border: none;
  outline: none;
}

/* ===== 리스트 ===== */
.list {
  width: var(--list-w);
  margin: 0 auto;
  flex: 1 1 auto;
  min-height: 0;
  overflow-x: hidden;
  overflow-y: auto;
  overscroll-behavior: contain;
  -webkit-overflow-scrolling: touch;
  align-content: start;
  padding-right: 8px;
  margin-top: clamp(14px, 3vh, 32px);
  display: grid;
  gap: 14px;
  flex-direction: column;
  height: 80%;
}

.list::-webkit-scrollbar {
  width: 6px;
}

/* ===== 카드 ===== */
*,
*::before,
*::after {
  box-sizing: border-box;
}

.card {
  width: 870px;
  height: 160px;
  background: rgba(255, 255, 255, .70);
  border-radius: 15px;
  border: 0.5px solid rgba(151, 151, 151, 0.4);
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 10px 18px;
  overflow: hidden;
  /* 최후의 방어 */
}

.card>* {
  min-width: 0;
}

/* 내용이 밖으로 못 튀게 */

.thumb {
  width: 140px;
  height: 140px;
  /* 고정 프레임(스크린샷과 유사) */
  object-fit: cover;
  border-radius: 10px;
  flex: 0 0 140px;
  display: block;
}

/* 오른쪽 전체 래퍼 */
.right {
  flex: 1 1 auto;
  min-width: 0;
  display: flex;
  flex-direction: column;
}

/* 상단: 제목 + 인원필 */
.cardHead {
  display: flex;
  align-items: flex-start;
  gap: 10px;
  height: auto;
  /* 고정 높이 제거 */
  margin-bottom: 0;
  min-width: 0;
}

.title {
  flex: 1 1 auto;
  min-width: 0;
  margin: 0 10px 0 0;
  color: #424242;
  font-size: 18px;
  line-height: 1.25;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-weight: 600;
  cursor: pointer;
  transition: color .2s ease, text-underline-offset .2s ease, opacity .2s ease;
}

.title:focus-visible {
  outline: 2px solid #2c9c61;
  outline-offset: 2px;
  border-radius: 4px;
}

.title:active {
  opacity: .85;
  transform: translateY(1px);
}

.title {
  position: relative;
}

.title::after {
  content: "";
  position: absolute;
  left: 0;
  right: 100%;
  bottom: -2px;
  height: 2px;
  background: currentColor;
  transition: right .25s ease;
}

.title:hover::after {
  right: 0;
}

.pill {
  flex: 0 0 auto;
  padding: 0 10px;
  height: 28px;
  border-radius: 20px;
  background: rgba(217, 217, 217, .50);
  color: #6D6D6D;
  display: inline-flex;
  align-items: center;
  font-weight: 600;
  font-size: 13px;
  white-space: nowrap;
}

.loading {
  width: 100%;
  margin: 0 auto;
  flex: 1 1 auto;
  min-height: 0;
  overflow-x: hidden;
  overflow-y: auto;
  overscroll-behavior: contain;
  -webkit-overflow-scrolling: touch;
  align-content: center;
  justify-content: center;
  padding-right: 8px;
  margin-top: clamp(5px, 2vh, 30px);
  display: grid;
  gap: 14px;
  flex-direction: column;
  height: 80%;
  color: grey;
}

/* 하단: 본문(left) + 가격(right) */
.under {
  display: flex;
  align-items: flex-start;
  gap: 1px;
  min-width: 0;
}

.cardBody {
  flex: 1 1 auto;
  min-width: 0;
  display: flex;
  flex-direction: column;
  gap: 6px;
  width: 430px;
}

/* 태그/주소 줄 */
.icon {
  display: flex;
  align-items: center;
  gap: 8px;
  flex-wrap: wrap;
}

.category {
  padding: 4px 10px;
  height: 24px;
  border-radius: 20px;
  background: #91a7889f;
  font-size: 12px;
  color: #1e4b29;
  margin: 5px 5px 3px 0;
}

.address {
  margin: 5px 5px 3px 0;

  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 4px 10px;
  height: 24px;
  border-radius: 20px;
  background: rgba(217, 217, 217, .50);
  color: #6D6D6D;
  font-size: 12px;
}

.addressIcon {
  width: 14px;
  height: 14px;
  display: block;
}

.roadaddress {
  margin: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* 본문 요약 */
.content {
  color: #6D6D6D;
  font-size: 13px;
  line-height: 1.4;
  min-width: 0;
  width: 400px;
  height: 52px;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  /* 스샷처럼 2줄만 */
  -webkit-box-orient: vertical;
  white-space: pre-line;
  overflow-wrap: anywhere;
  overflow: hidden;
}

/* 가격 박스(오른쪽) */
.priceBox {
  flex: 0 0 auto;
  min-width: 140px;
  margin-left: auto;
  margin-top: auto;
  display: flex;
  align-items: center;
  gap: 6px;
  padding-right: 10px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.price {
  color: #6D6D6D;
  font-family: Pretendard, system-ui, -apple-system, Segoe UI, Roboto, sans-serif;
  font-size: 18px;
  font-weight: 700;
  letter-spacing: .5px;
  margin: 0;
  margin-bottom: 20px;
  line-height: 1;
}

.slash {
  width: 34px;
  height: auto;
  margin: 2px 0 -2px;
  opacity: .9;
}

.totalPrice {
  color: #6D6D6D;
  font-size: 15px;
  font-weight: 600;
  letter-spacing: .2px;
  line-height: 1;
  white-space: nowrap;
  margin-top: 20px;
}

.sideBtn {
  position: absolute;
  bottom: 0%;
  left: calc(50% + var(--list-w) / 2 + 16px);
  transform: translateY(-50%);
  z-index: 3;
  width: 56px;
  height: 56px;
  border: 0;
  border-radius: 50%;
  background: #ffae00;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 32px;
  cursor: pointer;
  font-weight: 800;
  color: #212121;
  transition:
    transform .2s ease,
    box-shadow .2s ease,
    background-color .2s ease,
    color .2s ease,
    filter .2s ease;
  /* 살짝 그라데이션 주면 더 또렷함(선택) */
  background: linear-gradient(180deg, #ffe07a 0%, #ffd15a 100%);
}

.sideBtn:hover {
  transform: translateY(-50%) scale(1.06);
  /* 기존 translateY 유지 + 확대 */
  box-shadow: 0 16px 32px rgba(0, 0, 0, .28),
    0 0 0 6px rgba(255, 209, 90, .22);
  /* 바깥 링 느낌 */
  background: linear-gradient(180deg, #ffe78f 0%, #ffdb6f 100%);
}

.sideBtn:focus-visible {
  outline: none;
  box-shadow: 0 0 0 3px #ffffff,
    0 0 0 6px rgba(44, 156, 97, .35),
    0 14px 28px rgba(0, 0, 0, .22);
}

/* Active(누르는 순간): 살짝 눌림 */
.sideBtn:active {
  transform: translateY(-48%) scale(0.98);
  box-shadow: 0 8px 16px rgba(0, 0, 0, .25);
  filter: saturate(.95);
}

/* Disabled(선택): 비활성 상태 */
.sideBtn:disabled {
  cursor: not-allowed;
  opacity: .6;
  filter: grayscale(.1);
  box-shadow: none;
}

@media (max-width: 1200px) {
  .sideBtn {
    position: fixed;
    left: auto;
    right: 16px;
    bottom: 16px;
    top: auto;
    transform: none;
  }
}

.toast {
  position: fixed;
  top: 50px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(211, 211, 211, 0.482);
  color: #000000ff;
  padding: 12px 20px;
  border-radius: 999px;
  font-size: 15px;
  z-index: 9999;
  animation: fadein 0.5s, fadeout 0.5s 4.5s;
  /* 들어오고 4.5초 뒤 사라짐 */
}

@keyframes fadein {
  from {
    opacity: 0;
    transform: translateX(-50%) translateY(-10px);
  }

  to {
    opacity: 1;
    transform: translateX(-50%) translateY(0);
  }
}

@keyframes fadeout {
  from {
    opacity: 1;
    transform: translateX(-50%) translateY(0);
  }

  to {
    opacity: 0;
    transform: translateX(-50%) translateY(-10px);
  }
}